.controls {
  width: 100%;
  height: 100px;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.25);
  background-color: #062c56;
  display: flex;
  justify-content: center;
  align-items: center;
}
form {
  width: 675px;
  display: flex;
  justify-content: space-between;
}
input[type='number'] {
  width: 260px;
  height: 40px;
  background-color: #ffffff;
  border: none;
  border-radius: 3px;
  font-size: 16px;
  padding-left: 10px;
}
button[type='button'] {
  width: 187px;
  height: 40px;
  border-radius: 3px;
  background-color: #3884ff;
  font-size: 16px;
  letter-spacing: 0px;
  color: #f2efec;
  font-weight: 700;
  text-align: center;
}

button:hover {
  background-color: #3364d4;
}
